
if (typeof FastBoot === 'undefined') {
  if (!runningTests) {
    require('scenario-2-fastboot/app')['default'].create({"name":"scenario-2-fastboot","version":"v0.0.5+dc97e9b5"});
  }
}

define('~fastboot/app-factory', ['scenario-2-fastboot/app', 'scenario-2-fastboot/config/environment'], function(App, config) {
  App = App['default'];
  config = config['default'];

  return {
    'default': function() {
      return App.create(config.APP);
    }
  };
});

